'use client';

import { useState, useEffect } from 'react';
import axios from '@/lib/axios';
import { toast } from 'sonner';
import { ProdukResto, Kategori, Satuan } from '@/types';

export function useProdukPage() {
    // State Data
    const [produks, setProduks] = useState<ProdukResto[]>([]);
    const [kategoris, setKategoris] = useState<Kategori[]>([]);
    const [satuans, setSatuans] = useState<Satuan[]>([]);
    const [loading, setLoading] = useState(true);

    // Modal & Form State
    const [isDialogOpen, setIsDialogOpen] = useState(false);
    const [editData, setEditData] = useState<ProdukResto | null>(null);

    // Filter & Pagination
    const [searchQuery, setSearchQuery] = useState('');
    const [filterKategori, setFilterKategori] = useState<string>('all');
    const [currentPage, setCurrentPage] = useState(1);
    const [itemsPerPage] = useState(5);

    // 1. Fetch Data (Produk, Kategori, Satuan)
    const fetchAllData = async () => {
        setLoading(true);
        try {
            const [resProduk, resKat, resSat] = await Promise.all([
                axios.get('/api/produkRestos'),
                axios.get('/api/kategoris'),
                axios.get('/api/satuans')
            ]);
            setProduks(resProduk.data);
            setKategoris(resKat.data);
            setSatuans(resSat.data);
        } catch (error) {
            console.error("Error fetching data:", error);
            toast.error("Gagal memuat data produk.");
        } finally {
            setLoading(false);
        }
    };

    useEffect(() => {
        fetchAllData();
    }, []);

    // 2. Filter Logic
    const filteredProduks = produks.filter((item) => {
        const matchName = item.nama_produk.toLowerCase().includes(searchQuery.toLowerCase());
        const matchCategory = filterKategori === 'all' || item.kategori_id.toString() === filterKategori;
        return matchName && matchCategory;
    });

    // Pagination Logic
    useEffect(() => { setCurrentPage(1); }, [searchQuery, filterKategori]);
    const indexOfLastItem = currentPage * itemsPerPage;
    const indexOfFirstItem = indexOfLastItem - itemsPerPage;
    const currentItems = filteredProduks.slice(indexOfFirstItem, indexOfLastItem);

    // 3. Handlers
    const handleCreate = () => {
        setEditData(null);
        setIsDialogOpen(true);
    };

    const handleEdit = (item: ProdukResto) => {
        setEditData(item);
        setIsDialogOpen(true);
    };

    const handleDelete = async (id: number) => {
        if(!confirm("Yakin hapus produk ini?")) return;
        try {
            await axios.delete(`/api/produkRestos/${id}`);
            toast.success("Produk dihapus");
            fetchAllData();
        } catch {
            toast.error("Gagal menghapus produk (Mungkin sedang digunakan di transaksi).");
        }
    };

    const handleSubmit = async (formData: any, isEdit: boolean) => {
        try {
            if (isEdit && editData) {
                // UPDATE
                await axios.put(`/api/produkRestos/${editData.id}`, formData);
                
                // Upload Foto jika ada
                if (formData.foto) {
                    const imgData = new FormData();
                    imgData.append('foto', formData.foto);
                    await axios.post(`/api/produkRestos/${editData.id}/foto`, imgData, {
                        headers: { 'Content-Type': 'multipart/form-data' },
                    });
                }
                toast.success("Berhasil diperbarui!");
            } else {
                // CREATE
                const data = new FormData();
                Object.keys(formData).forEach(key => {
                    if (key === 'is_tersedia') {
                        data.append(key, formData[key] ? '1' : '0');
                    } else if (formData[key] !== null) {
                        data.append(key, formData[key]);
                    }
                });

                await axios.post('/api/produkRestos', data, {
                    headers: { 'Content-Type': 'multipart/form-data' },
                });
                toast.success("Berhasil ditambahkan!");
            }
            
            setIsDialogOpen(false);
            fetchAllData();
            
        } catch (error: any) {
            console.error(error);
            const msg = error.response?.data?.message || "Gagal menyimpan.";
            toast.error(msg);
        }
    };

    return {
        // Data & State
        produks: currentItems,
        totalData: filteredProduks.length,
        loading,
        kategoris,
        satuans,
        
        // Filter & Pagination
        currentPage,
        itemsPerPage,
        searchQuery,
        filterKategori,
        
        // Modal State
        isDialogOpen,
        editData,

        // Setters
        setSearchQuery,
        setFilterKategori,
        setCurrentPage,
        setIsDialogOpen,

        // Actions
        handleCreate,
        handleEdit,
        handleDelete,
        handleSubmit
    };
}